* {
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  margin: 0;
  background: #282D35;
}

/* Nav logo */
nav .nav-logo {
  max-width: 2em;
  height: 2em;
}
/* Nav Area */
nav {
  display: flex;
  align-items: center;
  height: 91px;
  background: #21222a;
  padding: 3em 3em;
}
nav .nav-logo-text,
nav .nav-text {
  margin: 0%;
}
nav .nav-logo-text {
  color: #21d9fa94;
  margin-right: auto;
  font-size: 22px;
  font-weight: 700;
}
nav .nav-text {
  color: #deebf8;
  font-weight: 600;
}

/* Main Content */
main{
    padding: 3em 0 4em 3em; 
    color: #fff;
    background-image: url(./img/react-icon-large.png);
    background-repeat: no-repeat;
    background-position: right;
}
main >h1{
    font-weight: 900;
    letter-spacing: -.05em;
    font-size: 39px;
}
main >ul{
    /* width: 400px; */
    padding: 0 0 0 40px;
    margin:0;
    
}
main >ul >li{
    padding: .7em 0 .7em 0;
}
main >ul >li::marker{
    color: #61DAFB;
    font-size: 1.4rem;
    padding: 0%;
    margin: 0;
}
